import React from "react";
import axios from "axios";
import { NavLink } from "react-router-dom";
import "./index.css"
import md5 from "md5";

function Home() {
    var url = "https://r671.pythonanywhere.com/i/getuser/"
    var islogined = window.localStorage.getItem("islogined")
    if(islogined != "true"){
      window.location.href = "/login"
    }else{
      var crsftoken = window.localStorage.getItem("token")
      var hcrsf = window.localStorage.getItem("hfr_id")
      var hmd51  = md5(crsftoken)
      if(hmd51 == `${hcrsf}`){
        axios.get(url, {"headers": {"Authorization": `Token ${crsftoken}`}}).then(res => {
          var funame = document.getElementById("funame")
          var fname = document.getElementById("fname")
          funame.innerHTML = res.data.username
          fname.innerHTML = res.data.name + " " + res.data.lastname
        })
      }else{window.location.href = "/login"}
    }

  return (
    <div className="homec">
        <div className="image"></div>
        <h2 id="funame"></h2>
        <h4 id="fname"></h4>
    </div>
  )
}

export default Home;