import React from "react";
import { NavLink } from "react-router-dom";
import "./index.css"
import md5 from "md5";
import axios from "axios";

function Register() {


  var islogined = window.localStorage.getItem("islogined")
  var hcrsf = window.localStorage.getItem("hfr_id")
  var crsftoken = window.localStorage.getItem("token")
  if(crsftoken != null){var hmd51  = md5(crsftoken)}
  if(islogined == "true" && hcrsf != hmd51){
    window.localStorage.removeItem("islogined")
    window.localStorage.removeItem("hfr_id")
    window.localStorage.removeItem("token")
  }
  if(islogined == "true" && hcrsf == hmd51){
    window.location.href = "/"
  }


    var iusername = document.getElementsByName("username")
    var iname = document.getElementsByName("name")
    var ilastname = document.getElementsByName("lastname")
    var iemail = document.getElementsByName("email")
    var ipassword = document.getElementsByName("password")


    var url = "https://r671.pythonanywhere.com/i/register/"

function submit(e){

  axios.post(url, {
    "username": iusername[0].value,
    "name": iname[0].value,
    "lastname": ilastname[0].value,
    "email": iemail[0].value,
    "password": ipassword[0].value
  }).then(response => {
    if(response.data.status == true){
      var hcrsf = md5(response.data.token)
        window.localStorage.setItem("token", response.data.token)
        window.localStorage.setItem("hfr_id", hcrsf)
        window.localStorage.setItem("islogined", true)
        window.location.reload(false)
   }

    if(response.data.status == false){
        var label = document.getElementById("luser")
        label.style.color = "red"
        label.innerHTML = "This username is already using"
    }
})
}

  return (
    <div name="center" className="center">
      <h1>Register</h1><br /><br />
        <label htmlFor="username" id="luser">Create username</label>
        <input type="text" id="username" name="username" />
        <label htmlFor="email">Enter your email</label>
        <input type="email" id="email" name="email" />

        <label htmlFor="name">Enter your name</label>
        <input type="text" id="name" name="name" />

        <label htmlFor="lastname">Enter your lastname</label>
        <input type="text" id="lastname" name="lastname" />

        <label htmlFor="password">Create password</label>
        <input type="password" id="password" name="password" />
        <div className="fbuton">
          <NavLink className="nav-link aininpt" to="/">Login</NavLink>
          <button id="sbutton"onClick={(e) => submit(e)}>Submit</button>
        </div>
    </div>
  )
}

export default Register;